

.project{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid #ccc;
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

.projet-browser{
    height: 20px;
    background-color: rgb(243, 242, 242) ;
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
}

.project-circle{
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: white;

}
.project-img{
    width: 100%;
    transition: all 10s ease ;
}

.project:hover .project-img{
    transform: translateY(-100%);
}


@media screen and (max-width : 480px){

    .project{
        width: 40%;
        height: 20vh;
    }

}